import {Pressable, View} from 'react-native';
import TextCM from '../../../Text';
import Markdown from 'react-native-markdown-display';
import {IAIResponseClass} from '../chat/helper';
import {useEffect, useState} from 'react';

type Props = {
  itemChat: IAIResponseClass;
  handleSendChat?: (question: string) => void;
  handleScrollToEnd?: () => void;
};

const ChatBubble = ({itemChat, handleSendChat, handleScrollToEnd}: Props) => {
  return (
    <>
      {itemChat.userId === -1 ? (
        <>
          {itemChat.isTyping ? (
            <TypingMarkdown
              handleScrollToEnd={handleScrollToEnd}
              markdownText={itemChat.message}
            />
          ) : (
            <Markdown
              style={{
                body: {
                  marginBottom: 10,
                  backgroundColor: '#e6eaf0',
                  borderRadius: 8,
                  paddingHorizontal: 16,
                  paddingVertical: 12,
                  maxWidth: '80%',
                },
              }}>
              {itemChat.message}
            </Markdown>
          )}
        </>
      ) : (
        <View
          style={{
            marginBottom: 10,
            backgroundColor: itemChat.userId === -1 ? '#e6eaf0' : '#6f2ced',
            borderRadius: 8,
            paddingHorizontal: 16,
            paddingVertical: 12,
            maxWidth: '80%',
            alignSelf: 'flex-end',
            flexShrink: 1,
          }}>
          {itemChat.userId !== -1 && itemChat.userId !== -2 && (
            <TextCM style={[itemChat.userId !== -1 && {color: '#fff'}]}>
              {itemChat.message}
            </TextCM>
          )}
          {itemChat.userId === -2 && (
            <Pressable onPress={() => handleSendChat?.(itemChat.message)}>
              <TextCM style={{color: '#fff'}}>{itemChat.message}</TextCM>
            </Pressable>
          )}
        </View>
      )}
    </>
  );
};

const TypingMarkdown = ({
  markdownText,
  speed = 2,
  handleScrollToEnd,
}: {
  markdownText: string;
  speed?: number;
  handleScrollToEnd?: () => void;
}) => {
  const [typedText, setTypedText] = useState('');

  useEffect(() => {
    let index = 0;
    const interval = setInterval(() => {
      if (index < markdownText.length) {
        setTypedText(prev => prev + markdownText.charAt(index));
        index++;
      } else {
        handleScrollToEnd?.();
        clearInterval(interval);
      }
    }, speed);

    return () => clearInterval(interval);
  }, [markdownText]);

  return (
    <Markdown
      style={{
        body: {
          marginBottom: 10,
          backgroundColor: '#e6eaf0',
          borderRadius: 8,
          paddingHorizontal: 16,
          paddingVertical: 12,
          maxWidth: '80%',
        },
      }}>
      {typedText}
    </Markdown>
  );
};

export default ChatBubble;
